* {
  @apply m-0 p-0;
  @apply box-border;
}

#__bg__ {
  @apply fixed;
  @apply top-0 left-0 -z-[9999];

  background: gold;
}

#__reload__ {
  @apply absolute;
  @apply right-[2px] top-[2px] p-1;

  &>button {
    background: #111;
    color: #fff;
    /*border-width: 0;*/
    border-width: .25em;
    border-color: #0a36e9;
    border-radius: 6px;
    border-style: solid;
    padding: 4px 8px;
    z-index: 9999;
    opacity: 1;

    &:hover {
      background: #0a36e9;
    }

    &:active {
      background: #0f6674;
    }
  }
}

